.suggestion-dropdown {
  width: 100%;
  font-size: 12px;
  background: #fff;
  border: 1px solid #999;
  border-top: none;
  position: absolute;
  display: none;
}

.autocomplete-search:focus-within .suggestion-dropdown {
  display: block;
  -webkit-box-shadow: 1px 1px 4px #aaa;
          box-shadow: 1px 1px 4px #aaa;
}



